<template>
  <div>
    <div class="container">
      <div class="top_top clearfix">
        <img src="../../assets/images/chapter01.png" class="fl">
        <ul class="fl">
          <li>
            <span class="first_">{{levelDesc + '(' + unionLevelDesc + ')'}}</span>
          </li>
          <li>
            <span class="two_">可创建一个 {{ number }} 位成员的联盟，成为盟主</span>
          </li>
          <li>
            <span class="two_">价格:</span>
            <span class="special">￥{{ money | formatPrice }}</span>
          </li>
          <li class="version">
            <span class="two_">版本:</span>
            <el-radio-group v-model="id">
              <el-radio-button v-for="item in payItems" :key="item.id" :label="item.id"> {{ item.desc }} </el-radio-button>
            </el-radio-group>
          </li>
          <li>
            <el-button type="primary" @click="buy">立即购买</el-button>
            <el-button @click="tryFree">升级至尊版 免费试用</el-button>
          </li>
        </ul>
        <p class="fr">若您对功能都买有任何疑问，请咨询克服400 898 4522</p>
      </div>
      <div class="bottom_bottom">
        <div>
          <span>商家联盟介绍</span>
          <p>多粉推出的商家联盟系统，是在移动互联网时代下，新零售的颠覆性创新理念。</p>
          <p>商家联盟可以让商家选择可靠、值得信任的伙伴组成联盟，</p>
          <p>整合了不同行业的资源，让商家一起共享客户资源，为联盟内的商家带来更多的商机机会。</p>
        </div>
        <p>应用页面</p>
        <div class="photo">
          <img src="../../assets/images/brandedCard01.png" alt="">
          <img src="../../assets/images/brandedCard02.png" alt="">
        </div>
        <div class="photo">
          <img src="../../assets/images/brandedCard03.png" alt="">
          <img src="../../assets/images/brandedCard04.png" alt="">
        </div>
      </div>
      <!-- 弹出框 扫码支付 -->
      <el-dialog title="付款" :visible.sync="visible1" size="tiny">
        <img v-bind:src="codeSrc" class="codeImg">
        <p>请使用微信扫描该二维码付款</p>
      </el-dialog>
      <!-- 弹出框 支付成功 -->
      <el-dialog title="消费核销成功" :visible.sync="visible2" size="tiny">
        <p>消费核销成功</p>
        <P> {{ countTime }} 秒后返回“我的联盟”页面... </P>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="create">创建联盟</el-button>
          <el-button @click="visible2=false">取 消</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import $http from '@/utils/http.js'
export default {
  data() {
    return {
      number: 0,
      payItems: [],
      levelDesc: '',
      unionLevelDesc: '',
      id: '',
      money: 0,
      visible1: false,
      visible2: false,
      codeSrc: '',
      only: '',
      countTime: 6,
      userId: '',
      socket: '',
      socketFlag: {},
    }
  },
  filters: {
    // 价格格式
    formatPrice: function(value) {
      if (!value) {
        return (Number(0)).toFixed(2)
      } else {
        return (Number(value)).toFixed(2);
      }
    }
  },
  mounted: function() {
    $http.get(`/unionMainCreate/instance`)
      .then(res => {
        if (res.data.data) {
          this.number = res.data.data.number;
          this.payItems = res.data.data.payItems;
          this.levelDesc = res.data.data.levelDesc;
          this.unionLevelDesc = res.data.data.unionLevelDesc;
        }
      })
      .catch(err => {
        this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
      })
  },
  watch: {
    id: function() {
      this.payItems.forEach((v, i) => {
        if (v.id === this.id) {
          this.money = v.money;
        }
      })
    }
  },
  methods: {
    // 购买
    buy: function() {
      $http.get(`/unionMainPermit/qrCode/${this.id}`)
        .then(res => {
          if (res.data.data) {
            this.codeSrc = res.data.data.url;
            this.only = res.data.data.only;
            this.userId = res.data.data.userId;
          } else {
            this.codeSrc = '';
            this.only = '';
            this.userId = '';
          }
        })
        .then(res => {
          this.visible = true;
        })
        .then(res => {
          if (!this.socket) {
            var socket = io.connect('https://socket.deeptel.com.cn'); // 测试
            // let socket = io.connect('http://183.47.242.2:8881'); // 堡垒
            var userId = this.userId;
            var _this = this;
            socket.on('connect', function() {
              let jsonObject = { userId: userId, message: "0" };
              socket.emit('auth', jsonObject);
              _this.socket = 1;
            });
          }
          socket.on('chatevent', function(data) {
            let msg = eval('(' + data.message + ')');
            if (!(_this.socketFlag.only == msg.only && _this.socketFlag.status == msg.status)) {
              if (_this.only == msg.only) {
                if (msg.status == '003') {
                  _this.$message({ showClose: true, message: '支付成功', type: 'success', duration: 5000 });
                  _this.visible1 = false;
                  _this.visible2 = true;
                  let timer3 = setInterval(() => {
                    if (_this.countTime == 0) {
                      clearInterval(timer3);
                      _this.$router.push({ path: '/my-union' });
                    }
                    _this.countTime--;
                  }, 1000);
                } else if (msg.status == '004') {
                  _this.$message({ showClose: true, message: '请求超时', type: 'warning', duration: 5000 });
                } else if (msg.status == '005') {
                  _this.$message({ showClose: true, message: '支付失败', type: 'warning', duration: 5000 });
                }
              };
            }
            _this.socketFlag.only = msg.only;
            _this.socketFlag.status = msg.status;
          });
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 免费使用
    tryFree: function() {
      $http.get(`/unionMainPermit/feeTradeUrl`)
        .then(res => {
          if (res.data.data) {
            topt.window.location = res.data.data;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 创建联盟
    create: function() {
      this.visible2 = false;
      $http.get(`/unionMainCreate/instance`)
        .then(res => {
          if (res.data.success && res.data.data.save) {
            this.$store.commit('permitIdChange', res.data.data.permitId);
            this.$router.push({ path: '/my-union/create-step' });
          } else if (res.data.success && res.data.data.pay) {
            this.$router.push({ path: '/my-union/no-register' });
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
  }
}
</script>
<style lang='less' rel="stylesheet/less" scoped>
.codeImg {
  width: 480px;
  height: 480px;
}

.container {
  margin: 40px 50px;
  .top_top {
    border: 1px solid #ddd;
    padding: 20px 20px 20px 50px;
    background: #F8F8F8;
    margin-bottom: 25px;
    p {
      margin-top: 170px;
      font-size: 14px;
      color: #666666;
    }
    .first_ {
      color: #333333;
    }
    .special {
      font-size: 24px;
      color: #ff4949;
    }
    .two_ {
      font-size: 14px;
      color: #666666;
    }
    ul {
      margin-left: 20px;
      height: 200px;
      li {
        margin-bottom: 13px;
      }
    }
  }
  .bottom_bottom {
    padding: 26px 0 26px 50px;
    border: 1px solid #ddd;
    div {
      span {
        font-size: 14px;
        color: #333333;
      }
      p {
        font-size: 14px;
        color: #333333;
        margin-top: 5px;
      }
    }
    >p {
      margin: 22px 0 30px 0;
      font-size: 14px;
      color: #333333;
    }
    .photo {
      img {
        margin-right: 15px;
        margin-bottom: 15px;
      }
    }
  }
}
</style>



